h2 {
  font-family: 'CenturyGothic';
  font-size: 48px;
  line-height: 120%;
  font-weight: 700;
}
.text27 {
  font-size: 27px;
  line-height: 120%;
  font-weight: 500;
  text-align: justify;
}
.btn-violet {
  position: relative;
  background: transparent;
  padding: 0;
  &:hover {
    .btn-text {
      background: #FF00F5;
    }
  }
  .btn-text {
    position: relative;
    z-index: 2;
    background: radial-gradient(50.00% 50.00% at 50% 50%,rgb(204, 0, 255) 39.469%,rgb(153, 29, 215) 100%);
    box-shadow: 0px 10px 0px 0px rgba(0, 0, 0, 0.15);
    border: 3px solid #FF00F5;
    line-height: 120%;
    font-weight: 700;
    color: #fff;
    transition: all 0.3s;
  }
  .btn-border {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    img {
      width: 100%;
    }
  }
}
.swiper-button-prev,
.swiper-button-next {
  width: 86px;
  &:after {
    display: none;
  }
  img {
    width: 100%;
  }
}
.up {
  position: fixed;
  z-index: 9;
  right: 25px;
  bottom: 50px;
  width: 91px;
  height: 91px;
  background: transparent;
  border: 7px solid #FF00C7;
  border-radius: 50%;
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s;
  &--active {
    opacity: 1;
    pointer-events: auto;
  }
  img {
    width: 40px;
    height: auto;
  }
}

@media screen and (max-width: 1400px) {
  h2 {
    font-size: 30px;
  }
  .text27 {
    font-size: 20px;
  }
  .swiper-button-prev,
  .swiper-button-next {
    width: 60px;
  }
}

@media screen and (max-width: 992px) {
  .swiper-button-prev,
  .swiper-button-next {
    width: 40px;
  }
}

@media screen and (max-width: 576px) {
  h2 {
    font-size: 16px;
  }
  .text27 {
    font-size: 12px;
  }
  .swiper-button-prev,
  .swiper-button-next {
    width: 30px;
  }
  .up {
    right: 10px;
    width: 29px;
    height: 29px;
    border-width: 2px;
    img {
      width: 14px;
    }
  }
}