.main {
  background-image: url("../images/body-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.main-section {
  background-image: url("../images/main-section__img.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 110px 0;
  &__text {
    font-size: 30px;
    line-height: 120%;
    font-weight: 700;
    text-transform: uppercase;
  }
  &__text2 {
    font-size: 100px;
    line-height: 100%;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 2px;
    margin-top: 40px;
    span {
      background: linear-gradient(145.59deg, rgb(255, 245, 0),rgb(255, 253, 198));
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      text-fill-color: transparent;
    }
  }
  &__btn {
    margin-top: 80px;
    margin-left: 40px;
    .btn-text {
      font-size: 32px;
      letter-spacing: 0.96px;
      padding: 22px 30px;
    }
    .btn-border {
      width: calc(100% + 154px);
      height: calc(100% + 100px);
    }
  }
}
.about {
  padding: 80px 0 120px;
  overflow: hidden;
  h2 {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 8px 14px;
    .stars {
      display: flex;
      align-items: center;
      gap: 8px;
    }
    .star {
      width: 46px;
      display: flex;
      justify-content: center;
      align-items: center;
      svg {
        width: 100%;
        height: auto;
      }
    }
  }
  &__text {
    margin-top: 30px;
    p {
      span {
        color: #FF00C7;
      }
    }
  }
  &__slider,
  &__slider2{
    position: relative;
    padding:  0 140px;
    margin: 80px 0;
    overflow: hidden;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  .swiper-slide {
    position: relative;
    height: auto;
    border: 6px solid #FF00C7;
    border-radius: 30px;
    padding-bottom: 56%;
    iframe,
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 30px;
    }
  }
  .swiper-button-prev {
    left: 0;
  }
  .swiper-button-next {
    right: 0;
  }
  &__list {
    margin-top: 30px;
    li {
      position: relative;
      padding-left: 46px;
      &:after {
        content: '';
        position: absolute;
        top: 8px;
        left: 0;
        width: 19px;
        height: 19px;
        background: #FF00C7;
        border-radius: 50%;
      }
      span {
        color: #FF00C7;
      }
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 80px;
  }
  &__slider2 {
    .swiper {
      overflow: unset;
    }
    .swiper-slide {
      opacity: 0;
      pointer-events: none;
      border: none !important;
      padding-bottom: 27%;
      border-radius: 20px;
      transition: all 0.3s;
      img {
        box-shadow: 0px 0px 10px 0px rgba(255, 255, 255, 0.5);
        border: 2px solid #FF00C7;
        border-radius: 20px;
      }
    }
    .swiper-slide-active,
    .swiper-slide-active + .swiper-slide,
    .swiper-slide-active + .swiper-slide + .swiper-slide,
    .swiper-slide-active + .swiper-slide + .swiper-slide + .swiper-slide,
    .swiper-slide-active + .swiper-slide + .swiper-slide + .swiper-slide + .swiper-slide {
      opacity: 1;
      pointer-events: auto;
    }
  }
}
.banner {
  position: relative;
  background-image: url("../images/banner-img.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  border-radius: 30px;
  padding: 70px 70px 100px 110px;
  margin: 80px 0;
  &2 {
    background-image: url("../images/banner-img2.webp");
  }
  &__text {
    font-size: 100px;
    line-height: 100%;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 2px;
    background: linear-gradient(145.59deg, rgb(255, 245, 0),rgb(255, 253, 198));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
  }
  &__btn {
    margin-top: 80px;
    margin-left: 35px;
    .btn-text {
      font-size: 27px;
      letter-spacing: 0.81px;
      padding: 16px 20px;
    }
    .btn-border {
      width: calc(100% + 130px);
      height: calc(100% + 80px);
    }
  }
}
.comments {
  margin-top: 80px;
  .comments-sort {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 10px 20px;
  }
  h3 {
    font-size: 27px;
    line-height: 120%;
    font-weight: 500;
  }
  h5 {
    font-size: 27px;
    line-height: 120%;
    font-weight: 500;
  }
  .select {
    position: relative;
  }
  .select--active {
    .select-values {
      transform: translateY(0);
      opacity: 1;
      pointer-events: auto;
    }
    .base-value {
      border-radius: 10px 10px 0 0;
      &:after {
        transform: translate(0, -50%) rotate(-180deg);
      }
    }
  }
  .base-value {
    position: relative;
    z-index: 1;
    gap: 8px;
    background: rgba(255, 255, 255, 0.05);
    border: 2px solid #FF00C7;
    border-radius: 10px;
    padding: 18px 58px 18px 20px;
    font-size: 27px;
    line-height: 120%;
    font-weight: 500;
    cursor: pointer;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      right: 17px;
      transform: translate(0, -50%);
      width: 24px;
      height: 28px;
      background-image: url("../images/select-icon.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      transition: all 0.15s;
    }

  }
  .select-values {
    position: absolute;
    top: 100%;
    left: 0;
    transform: translateY(-10px);
    width: 100%;
    border: 2px solid #FF00C7;
    border-top: unset;
    border-radius: 0 0 10px 10px;
    cursor: pointer;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
  }
  .value {
    font-size: 20px;
    color: rgba(255, 255, 255, .8);
    background: rgba(255, 255, 255, 0.05);
    border-bottom: 1px solid rgba(80, 80, 80, .7);
    padding: 12px;
    cursor: pointer;
  }
  .comments-list {
    display: grid;
    grid-template-columns: 100%;
    gap: 13px;
    padding: 0;
    margin: 80px 0 0 0;
  }
  .comment {
    display: grid;
    grid-template-columns: 100%;
    gap: 13px;
  }
  .comment-article,
  .comment-form {
    background: rgba(255, 255, 255, 0.05);
    border: 4px solid #FF00C7;
    border-radius: 25px;
    padding: 33px 44px;
  }
  .comment-header {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    gap: 18px;
  }
  .comment-user {
    display: flex;
    gap: 18px;
    &-image {
      max-width: 51px;
      flex-shrink: 0;
      img {
        max-width: 100%;
      }
    }
    &-info {
      padding-top: 8px;
      h6 {
        font-family: 'CenturyGothic';
        font-size: 20px;
        line-height: 120%;
        font-weight: 700;
      }
      .comment-user-info-date {
        font-family: 'CenturyGothic';
        font-size: 17px;
        line-height: 120%;
        color: rgba(255, 255, 255, 0.5);
        margin-top: 4px;
      }
    }
  }
  .comment-vote {
    display: flex;
    align-items: center;
    gap: 8px;
    img {
      width: 27px;
    }
  }
  .comment-content {
    margin-top: 24px;
    p {
      font-size: 20px;
      line-height: 120%;
      font-weight: 500;
      text-align: justify;
    }
  }
  .comment-footer {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    gap: 70px;
    margin-top: 24px;
  }
  .comment-footer-like {
    display: flex;
    align-items: flex-end;
    gap: 10px;
    img {
      width: 28px;
    }
    button {
      background: transparent;
      padding: 0;
      font-size: 20px;
      line-height: 120%;
      font-weight: 500;
      color: #fff;
    }
  }
  .comment-likes {
    font-size: 20px;
    line-height: 120%;
    font-weight: 500;
  }
  .comment-footer-answer {
    display: flex;
    align-items: flex-end;
    gap: 10px;
    img {
      width: 28px;
    }
    button {
      background: transparent;
      padding: 0;
      font-size: 20px;
      line-height: 120%;
      font-weight: 500;
      color: #fff;
    }
  }
  .children {
    padding-left: 140px;
  }
  .comments-showmore {
    width: 86px;
    margin: 37px auto 0;
    cursor: pointer;
    img {
      width: 100%;
    }
  }
  .comment-form {
    margin-top: 50px;
  }
  .comment-form-title {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-end;
    gap: 4px 8px;
  }
  .stars-container {
    display: flex;
    align-items: center;
    gap: 8px;
  }
  .star {
    width: 37px;
    height: 37px;
    display: block;
    background-image: url(../images/empty-star.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    cursor: pointer;
  }
  .comment-form-container {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: start;
    margin-top: 24px;
  }
  .comment-form-left {
    width: 404px;
    flex-shrink: 0;
    display: grid;
    grid-template-columns: 100%;
    gap: 12px;
    margin-right: 70px;
    input[type=email],
    input[type=tel],
    input[type=text] {
      width: 100%;
      height: 41px;
      background: rgba(0, 0, 0, 0.2);
      border: 2px solid #d536ff;
      border-radius: 10px;
      padding: 0 32px;
      font-size: 27px;
      line-height: 120%;
      font-weight: 500;
      color: #fff;
      &::placeholder {
        color: rgba(255, 255, 255, 0.5);
      }
    }
  }
  .comment-form-right {
    width: calc(100% - 474px);
    textarea {
      width: 100%;
      height: 173px;
      background: rgba(0, 0, 0, 0.2);
      border: 2px solid #d536ff;
      border-radius: 10px;
      padding: 14px 32px;
      font-size: 27px;
      line-height: 120%;
      font-weight: 500;
      color: #fff;
      resize: none;
      &::placeholder {
        color: rgba(255, 255, 255, 0.5);
      }
    }
  }
  .checkbox {
    width: 100%;
    display: flex;
    background: rgba(0, 0, 0, 0.2);
    border: 2px solid #d536ff;
    border-radius: 10px;
    padding: 10px 32px;
    input {
      position: absolute;
      z-index: 3;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      cursor: pointer;
      opacity: 0;

    }
    input:checked ~ span {
      &:after {
        background-image: url("../images/check.svg");
      }
    }
    span {
      position: relative;
      font-size: 27px;
      line-height: 120%;
      font-weight: 500;
      padding-left: 50px;
      &:after {
        content: '';
        position: absolute;
        top: -1px;
        left: 0;
        width: 35px;
        height: 35px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        border: 2px solid #FF00C7;
        border-radius: 5px;
        transition: all 0.3s;
      }
    }
  }
  .comment-form-btn {
    .btn-text {
      width: 100%;
      border-radius: 10px;
      padding: 10px 14px;
      font-size: 27px;
    }
  }
}


