header {
  position: relative;
  z-index: 5;
  background: rgb(54, 16, 57);
  box-shadow: 0px 0px 40px 10px rgb(255, 0, 199);
  border-bottom: 2px solid rgb(255, 0, 245);
  padding: 8px 0;
  overflow-x: hidden;
  .header__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 96px;
  }
  .header__logo {
    width: 254px;
    flex-shrink: 0;
    display: block;
    img {
      width: 100%;
    }
  }
  .header__list {
    display: flex;
    flex-grow: 1;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
    li a {
      font-family: 'CenturyGothic';
      font-size: 18px;
      line-height: 120%;
      font-weight: 700;
      &:hover {
        color: #FF00C7;
      }
    }
  }
}
.burger {
  position: relative;
  z-index: 15;
  width: 67px;
  height: 45px;
  display: none;
  cursor: pointer;
  span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 8px;
    background: #FF00C7;
    border-radius: 1px;
    transition: all 0.3s;
  }
  span:nth-child(1) {
    top: 0;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
  }
  span:nth-child(3) {
    bottom: 0;
  }
  &--active {
    span {
      background: #fff;
    }
    span:nth-child(1) {
      top: 18px;
      transform: rotate(-45deg);
    }
    span:nth-child(2) {
      display: none;
    }
    span:nth-child(3) {
      bottom: 18px;
      transform: rotate(45deg);
    }
  }
}

@media screen and (max-width: 1600px) {
  header {
    .header__inner {
      gap: 20px;
    }
    .header__list {
      flex-grow: unset;
      gap: 20px;
      li a {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 1300px) {
  header {
    .header__logo {
      width: 200px;
    }
    .header__inner {
      gap: 15px;
    }
    .header__list {
      gap: 15px;
    }
  }
}

@media screen and (max-width: 1200px) {
  header {
    .header__list {
      position: fixed;
      z-index: 10;
      top: 0;
      left: -105%;
      width: 65%;
      height: 100%;
      flex-direction: column;
      justify-content: start;
      align-items: start;
      gap: 50px;
      background: rgb(54, 16, 57);
      box-shadow: 0px 0px 40px 10px rgb(255, 0, 199);
      padding: 130px 25px 50px;
      transition: all 0.5s;
      overflow-y: auto;
      &--active {
        left: 0;
      }
      li a {
        font-size: 32px;
      }
    }
  }
  .burger {
    display: block;
  }
}

@media screen and (max-width: 768px) {
  header {
    .header__list {
      gap: 30px;
      li a {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  header {
    .header__logo {
      width: 120px;
    }
    .header__list {
      width: 77%;
      padding: 80px 25px 30px;
    }
  }
  .burger {
    width: 26px;
    height: 17px;
    span {
      height: 3px;
    }
    &--active {
      span:nth-child(1) {
        top: 7px;
      }
      span:nth-child(3) {
        bottom: 7px;
      }
    }
  }
}