footer {
  position: relative;
  background-color: #2A0A3E;
  padding: 70px 0;
  overflow: hidden;
  &:before {
    content: '';
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url("../images/footer-bg.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    pointer-events: none;
  }
  &:after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 50%;
    background: linear-gradient(0deg, rgba(255, 0, 199, 0.3),rgba(109, 17, 133, 0) 100%);
    pointer-events: none;
  }
}
.footer-top {
  position: relative;
  z-index: 2;
  &__items {
    display: flex;
    justify-content: space-between;
    gap: 24px;
  }
  &__group {
    display: grid;
    grid-template-columns: 100%;
    gap: 70px;
  }
  &__subtitle {
    font-size: 17px;
    line-height: 120%;
    font-weight: 700;
    color: #FF00C7;
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    margin-top: 30px;
    li a {
      font-size: 17px;
      line-height: 120%;
      font-weight: 500;
      &:hover {
        color: #FF00C7;
      }
    }
  }
}
.footer-bottom {
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: 70px;
  margin-top: 70px;
  &__cards {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 150px;
  }
  &__card {
    max-width: 182px;
    img {
      max-width: 100%;
    }
  }
  &__info {
    width: 79px;
    img {
      width: 100%;
    }
  }
  &__text {
    font-size: 20px;
    line-height: 120%;
    font-weight: 500;
    letter-spacing: -1px;
  }
}

@media screen and (max-width: 1400px) {
  footer {
    padding: 60px 0;
  }
  .footer-top {
    &__subtitle {
      font-size: 14px;
    }
    &__list {
      li a {
        font-size: 14px;
      }
    }
  }
  .footer-bottom {
    gap: 30px;
    margin-top: 60px;
    &__cards {
      gap: 30px;
    }
    &__text {
      font-size: 14px;
      letter-spacing: -0.7px;
    }
  }
}

@media screen and (max-width: 992px) {
  .footer-top {
    &__items {
      display: grid;
      grid-template-columns: repeat(4, calc(25% - 72px / 4));
      gap: 24px;
      align-items: start;
    }
    &__group {
      gap: 24px;
    }
  }
}

@media screen and (max-width: 768px) {
  .footer-top {
    &__items {
      grid-template-columns: repeat(3, calc(100% / 3 - 48px / 3));
    }
  }
}

@media screen and (max-width: 576px) {
  .footer-top {
    &__items {
      grid-template-columns: repeat(2, calc(50% - 24px / 2));
    }
  }
}

@media screen and (max-width: 359px) {
  .footer-top {
    &__subtitle {
      font-size: 12px;
    }
    &__list {
      li a {
        font-size: 12px;
      }
    }
  }
}